body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
    background-color: #120312;
}
.container_side {
    position: relative;
    width: 100%;
    overflow: hidden;
}

/* width mide el ancho del navbar */
.sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 40px;
    background: #120312;
    z-index: 100;
}

.sidebar .logo-details {
    height: 100px;
    width: 100%;
    display: flex;
    align-items: center;
}

/* min-width centra el logo */
.logo-details i {
    font-size: 35px;
    color: #fff;
    height: 50px;
    min-width: 39px;
    text-align: center;
    line-height: 50px;
}

.sidebar .nav-links {
    height: 100%;
    padding: 50px 0 150px 0;
    overflow: auto;
}

.sidebar .nav-links {
    overflow: visible;
}

.sidebar .nav-links li {
    position: relative;
    list-style: none;
    transition: all 0.4s ease;
}

.sidebar .nav-links li:hover {
    background: #200A20;
}

.sidebar .nav-links li .icon-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sidebar.close .nav-links li .icon-link {
    display: block
}

/* line-heat es lo que genera el espacio entre cada icono */
/* min-width centra los iconos */
.sidebar .nav-links li i {
    height: 80px;
    min-width: 39px;
    text-align: center;
    line-height: 78px;
    color: #fff;
    font-size: 25px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.sidebar .nav-links li .linkS {
    display: flex;
    align-items: center;
    text-decoration: none;
}

/* PopUp que dice el nombre decada icono */
/* margin top es lo que alinea el cartel del icono */
.sub-menu {
    padding: 10px 15px 10px 15px;
    margin-top: 10px;
    background: #fff;
    display: none;
    position: absolute;
    left: 102%;
    margin-top: 20px;
    border-radius: 6px 6px 6px 6px;
    opacity: 0;
    display: block;
    pointer-events: none;
    transition: 0s;
    align-items: center;
}

.nav-links li:hover .sub-menu {
    top: 0;
    opacity: 1;
    pointer-events: auto;
    transition: all 0.4s ease;
}

.sub-menu li {
    color: #200A20;
    pointer-events: none;
    font-family: 'Public Sans', sans-serif;
    font-size: 13px;
}
footer {
  background: #120312;
  width: 100%;
  bottom: 0;
  left: 0;
}

footer .content {
  margin: auto;
  padding: 15px 40px 0px 40px;
}

footer .content .top {
  display: flex;
  align-items: center;
  justify-content: center;
}

.content .top .media-icons {
  display: flex;
}

/* font-size cambia el tamaño de los iconos de link y git */
.content .top .media-icons a {
  height: 40px;
  width: 40px;
  margin: 0 8px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  color: #fff;
  font-size: 30px;
  text-decoration: none;
  transition: all 0.4s ease;
}

.top .media-icons a:nth-child(1) {
  background: #120312;
}

.top .media-icons a:nth-child(2) {
  background: #120312;
}

footer .bottom-details {
